'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'

interface RoomSchedule {
  id: string
  startTime: string
  endTime: string
  instructor: string
  subject: string
  studentsCount: number
}

interface Room {
  id: string
  name: string
  capacity: number
  status: 'available' | 'occupied'
  currentSchedule?: RoomSchedule
  nextAvailable?: string
  todaySchedule: RoomSchedule[]
}

export default function RoomsPage() {
  const [rooms, setRooms] = useState<Room[]>([])
  const [currentTime, setCurrentTime] = useState(new Date())
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Update current time every minute
    const timer = setInterval(() => {
      setCurrentTime(new Date())
    }, 60000)

    return () => clearInterval(timer)
  }, [])

  useEffect(() => {
    // Simulate loading rooms from API
    const loadRooms = async () => {
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      const mockRooms: Room[] = [
        {
          id: '1',
          name: 'Room A',
          capacity: 30,
          status: 'occupied',
          currentSchedule: {
            id: '1',
            startTime: '09:00',
            endTime: '10:30',
            instructor: 'Dr. Mohamed Ali',
            subject: 'Mathematics',
            studentsCount: 25
          },
          nextAvailable: '10:30',
          todaySchedule: [
            {
              id: '1',
              startTime: '09:00',
              endTime: '10:30',
              instructor: 'Dr. Mohamed Ali',
              subject: 'Mathematics',
              studentsCount: 25
            },
            {
              id: '2',
              startTime: '11:00',
              endTime: '12:30',
              instructor: 'Prof. Sarah Ahmed',
              subject: 'Physics',
              studentsCount: 20
            },
            {
              id: '3',
              startTime: '14:00',
              endTime: '15:30',
              instructor: 'Mr. Omar Khaled',
              subject: 'Chemistry',
              studentsCount: 22
            }
          ]
        },
        {
          id: '2',
          name: 'Room B',
          capacity: 25,
          status: 'available',
          todaySchedule: [
            {
              id: '4',
              startTime: '13:00',
              endTime: '14:30',
              instructor: 'Dr. Amina Hassan',
              subject: 'Biology',
              studentsCount: 18
            },
            {
              id: '5',
              startTime: '15:00',
              endTime: '16:30',
              instructor: 'Prof. Youssef Ibrahim',
              subject: 'English',
              studentsCount: 24
            }
          ]
        },
        {
          id: '3',
          name: 'Room C',
          capacity: 35,
          status: 'occupied',
          currentSchedule: {
            id: '6',
            startTime: '10:00',
            endTime: '11:30',
            instructor: 'Dr. Khaled Nasser',
            subject: 'Arabic',
            studentsCount: 28
          },
          nextAvailable: '11:30',
          todaySchedule: [
            {
              id: '6',
              startTime: '10:00',
              endTime: '11:30',
              instructor: 'Dr. Khaled Nasser',
              subject: 'Arabic',
              studentsCount: 28
            },
            {
              id: '7',
              startTime: '12:00',
              endTime: '13:30',
              instructor: 'Prof. Mona Farid',
              subject: 'History',
              studentsCount: 30
            }
          ]
        },
        {
          id: '4',
          name: 'Room D',
          capacity: 20,
          status: 'available',
          todaySchedule: [
            {
              id: '8',
              startTime: '16:00',
              endTime: '17:30',
              instructor: 'Mr. Ahmed Samir',
              subject: 'Computer Science',
              studentsCount: 15
            }
          ]
        }
      ]
      
      setRooms(mockRooms)
      setIsLoading(false)
    }
    
    loadRooms()
  }, [])

  const getStatusColor = (status: string) => {
    return status === 'available' 
      ? 'bg-green-100 text-green-800' 
      : 'bg-red-100 text-red-800'
  }

  const getUtilizationColor = (percentage: number) => {
    if (percentage >= 80) return 'bg-red-500'
    if (percentage >= 60) return 'bg-yellow-500'
    return 'bg-green-500'
  }

  const calculateUtilization = (room: Room) => {
    const totalMinutes = room.todaySchedule.reduce((total, schedule) => {
      const start = new Date(`2024-01-01 ${schedule.startTime}`)
      const end = new Date(`2024-01-01 ${schedule.endTime}`)
      return total + (end.getTime() - start.getTime()) / (1000 * 60)
    }, 0)
    
    // Assuming 8 hours working day (480 minutes)
    return Math.round((totalMinutes / 480) * 100)
  }

  const formatTime = (time: string) => {
    return new Date(`2024-01-01 ${time}`).toLocaleTimeString('en-US', {
      hour: 'numeric',
      minute: '2-digit',
      hour12: true
    })
  }

  const stats = {
    totalRooms: rooms.length,
    availableRooms: rooms.filter(r => r.status === 'available').length,
    occupiedRooms: rooms.filter(r => r.status === 'occupied').length,
    averageUtilization: Math.round(rooms.reduce((sum, room) => sum + calculateUtilization(room), 0) / rooms.length)
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-orange-500 mx-auto mb-4"></div>
          <p className="text-lg text-slate-600">Loading rooms...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
        <div>
          <h1 className="text-3xl font-bold bg-gradient-to-r from-slate-900 to-slate-600 bg-clip-text text-transparent">
            Room Management
          </h1>
          <p className="text-slate-600 mt-2">
            Monitor and manage room schedules and availability
          </p>
        </div>
        <div className="text-right">
          <div className="text-sm text-slate-500">Current Time</div>
          <div className="text-lg font-semibold text-slate-900">
            {currentTime.toLocaleTimeString('en-US', {
              hour: 'numeric',
              minute: '2-digit',
              hour12: true
            })}
          </div>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card className="border-0 shadow-lg bg-gradient-to-r from-blue-50 to-cyan-50">
          <CardContent className="p-6 text-center">
            <div className="text-2xl font-bold text-blue-600">{stats.totalRooms}</div>
            <div className="text-sm text-slate-600">Total Rooms</div>
          </CardContent>
        </Card>
        <Card className="border-0 shadow-lg bg-gradient-to-r from-green-50 to-emerald-50">
          <CardContent className="p-6 text-center">
            <div className="text-2xl font-bold text-green-600">{stats.availableRooms}</div>
            <div className="text-sm text-slate-600">Available Now</div>
          </CardContent>
        </Card>
        <Card className="border-0 shadow-lg bg-gradient-to-r from-red-50 to-pink-50">
          <CardContent className="p-6 text-center">
            <div className="text-2xl font-bold text-red-600">{stats.occupiedRooms}</div>
            <div className="text-sm text-slate-600">Currently Occupied</div>
          </CardContent>
        </Card>
        <Card className="border-0 shadow-lg bg-gradient-to-r from-purple-50 to-indigo-50">
          <CardContent className="p-6 text-center">
            <div className="text-2xl font-bold text-purple-600">{stats.averageUtilization}%</div>
            <div className="text-sm text-slate-600">Avg Utilization</div>
          </CardContent>
        </Card>
      </div>

      {/* Rooms Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {rooms.map((room) => {
          const utilization = calculateUtilization(room)
          return (
            <Card key={room.id} className="border-0 shadow-lg hover:shadow-xl transition-all duration-300">
              <CardHeader className="pb-4">
                <div className="flex items-center justify-between">
                  <div>
                    <CardTitle className="text-xl font-bold text-slate-900">
                      {room.name}
                    </CardTitle>
                    <CardDescription className="text-slate-600">
                      Capacity: {room.capacity} students
                    </CardDescription>
                  </div>
                  <Badge className={`${getStatusColor(room.status)} border-0 text-sm px-3 py-1`}>
                    {room.status === 'available' ? 'Available' : 'Occupied'}
                  </Badge>
                </div>
              </CardHeader>
              
              <CardContent className="space-y-4">
                {/* Current Status */}
                {room.status === 'occupied' && room.currentSchedule && (
                  <div className="bg-red-50 p-4 rounded-lg border border-red-200">
                    <h4 className="font-medium text-red-800 mb-2">Currently Occupied</h4>
                    <div className="text-sm text-red-700 space-y-1">
                      <p><strong>Subject:</strong> {room.currentSchedule.subject}</p>
                      <p><strong>Instructor:</strong> {room.currentSchedule.instructor}</p>
                      <p><strong>Time:</strong> {formatTime(room.currentSchedule.startTime)} - {formatTime(room.currentSchedule.endTime)}</p>
                      <p><strong>Students:</strong> {room.currentSchedule.studentsCount}</p>
                    </div>
                    {room.nextAvailable && (
                      <div className="mt-2 pt-2 border-t border-red-200">
                        <p className="text-sm font-medium text-red-800">
                          Available at: {formatTime(room.nextAvailable)}
                        </p>
                      </div>
                    )}
                  </div>
                )}

                {room.status === 'available' && (
                  <div className="bg-green-50 p-4 rounded-lg border border-green-200">
                    <h4 className="font-medium text-green-800 mb-2">Available Now</h4>
                    <p className="text-sm text-green-700">
                      This room is currently available for use.
                    </p>
                  </div>
                )}

                {/* Utilization */}
                <div>
                  <div className="flex justify-between items-center mb-2">
                    <span className="text-sm font-medium text-slate-700">Today's Utilization</span>
                    <span className="text-sm text-slate-600">{utilization}%</span>
                  </div>
                  <div className="w-full bg-slate-200 rounded-full h-2">
                    <div 
                      className={`h-2 rounded-full ${getUtilizationColor(utilization)}`}
                      style={{ width: `${utilization}%` }}
                    ></div>
                  </div>
                </div>

                {/* Today's Schedule */}
                <div>
                  <h4 className="font-medium text-slate-900 mb-3">Today's Schedule</h4>
                  {room.todaySchedule.length > 0 ? (
                    <div className="space-y-2">
                      {room.todaySchedule.map((schedule, index) => (
                        <div key={schedule.id} className="flex items-center justify-between p-3 bg-slate-50 rounded-lg">
                          <div className="flex-1">
                            <div className="font-medium text-slate-900 text-sm">
                              {schedule.subject}
                            </div>
                            <div className="text-xs text-slate-600">
                              {schedule.instructor} â€¢ {schedule.studentsCount} students
                            </div>
                          </div>
                          <div className="text-right">
                            <div className="text-sm font-medium text-slate-900">
                              {formatTime(schedule.startTime)}
                            </div>
                            <div className="text-xs text-slate-600">
                              {formatTime(schedule.endTime)}
                            </div>
                          </div>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <p className="text-sm text-slate-500 italic">No classes scheduled for today</p>
                  )}
                </div>

                {/* Action Buttons */}
                <div className="flex space-x-2 pt-2">
                  <Button variant="outline" className="flex-1 text-sm">
                    View Full Schedule
                  </Button>
                  <Button variant="outline" className="flex-1 text-sm">
                    Book Room
                  </Button>
                </div>
              </CardContent>
            </Card>
          )
        })}
      </div>

      {/* Quick Actions */}
      <Card className="border-0 shadow-lg">
        <CardHeader>
          <CardTitle className="text-xl font-bold text-slate-900">
            Quick Actions
          </CardTitle>
          <CardDescription>
            Manage room schedules and bookings
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Button className="bg-gradient-to-r from-blue-500 to-cyan-500 hover:from-blue-600 hover:to-cyan-600 text-white p-6 h-auto">
              <div className="text-center">
                <div className="font-semibold">Book a Room</div>
                <div className="text-sm opacity-90">Reserve room for class</div>
              </div>
            </Button>
            <Button variant="outline" className="p-6 h-auto">
              <div className="text-center">
                <div className="font-semibold">View Weekly Schedule</div>
                <div className="text-sm text-slate-600">See all room bookings</div>
              </div>
            </Button>
            <Button variant="outline" className="p-6 h-auto">
              <div className="text-center">
                <div className="font-semibold">Room Reports</div>
                <div className="text-sm text-slate-600">Utilization analytics</div>
              </div>
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
